<template>
    <div>
        <el-breadcrumb >
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>参数列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card style="margin-top:20px">
              <el-alert
                title="注意：只允许为第三级分类设置相关参数"
                type="success" :closable="false">
            </el-alert>
            <el-row style="margin:20px 0">
                <el-col>
                    <span>选择商品分类:</span>
                     <el-cascader
                            v-model="value"
                            :options="cateList"
                            expand-trigger="hover"
                            :props="CateProps"
                            clearable
                            @change="handleChange">
                    </el-cascader>
                </el-col>
            </el-row>
            <template>
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="动态参数" name="many">
                <el-button type="primary" :disabled="isBtn" size="mini"  @click="dialogVisible = true">添加参数</el-button>
                <el-table :data="manyTbale">
                     <el-table-column type="expand">
                         <template slot-scope="scope">
                             <el-tag  style=" margin-right:10px" @close="handelClose(i,scope.row)" closable v-for="(item,i) in scope.row.attr_vals" :key="i">{{item}}</el-tag>
                             <el-input
                                class="input-new-tag"
                                v-if="scope.row.inputVisible"
                                v-model="scope.row.inputValue"
                                ref="saveTagInput"
                                size="small"
                                @keyup.enter.native="handleInputConfirm(scope.row)"
                                @blur="handleInputConfirm(scope.row)"
                                style="width:120px"
                                >
                                </el-input>
                                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                         </template>
                         
                     </el-table-column>
                    <el-table-column type="index"></el-table-column>
                    <el-table-column label="参数名称" prop="attr_name"></el-table-column>
                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <el-button size="mini" type="primary" icon="el-icon-edit"  @click="edit(scope.row.attr_id)">编辑</el-button>
                             <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteAction(scope.row.attr_id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane label="静态属性" name="only" >
                 <el-button type="primary" :disabled="isBtn"  size="mini"  @click="dialogVisible = true">添加属性</el-button>
                 <el-table :data="onlyTable">
                     <el-table-column type="expand">
                          <template slot-scope="scope">
                             <el-tag  style=" margin-right:10px" @close="handelClose(i,scope.row)" closable v-for="(item,i) in scope.row.attr_vals" :key="i">{{item}}</el-tag>
                             <el-input
                                class="input-new-tag"
                                v-if="scope.row.inputVisible"
                                v-model="scope.row.inputValue"
                                ref="saveTagInput"
                                size="small"
                                @keyup.enter.native="handleInputConfirm(scope.row)"
                                @blur="handleInputConfirm(scope.row)"
                                style="width:120px"
                                >
                                </el-input>
                                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                         </template>
                     </el-table-column>
                    <el-table-column type="index"></el-table-column>
                    <el-table-column label="属性名称" prop="attr_name"></el-table-column>
                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <el-button size="mini" type="primary" icon="el-icon-edit"  @click="edit(scope.row.attr_id)">编辑</el-button>
                             <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteAction(scope.row.attr_id)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
        </el-tabs>
            </template>
        </el-card>
        <el-dialog
            :title="'添加' + titleData"
            :visible.sync="dialogVisible"
            width="50%"
            @close="addClose"
            >
            <el-form ref="addFormRef" :model="form" label-width="80px" :rules="addFrom" >
                <el-form-item :label="titleData" prop="attr_name">
                    <el-input v-model="form.attr_name"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addParams">确 定</el-button>
            </span>
        </el-dialog>
        <el-dialog
            :title="'添加' + titleData"
            :visible.sync="editDialogVisible"
            width="50%"
            @close="editClose"
            >
            <el-form ref="editFormRef" :model="editform" label-width="80px" :rules="editFrom" >
                <el-form-item :label="titleData" prop="attr_name">
                    <el-input v-model="editform.attr_name"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editParams">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import { MessageBox } from 'element-ui'
export default {

    data() {
        return {
            editform:{
                
            },
            editDialogVisible:false,
            form:{
               attr_name:''
            },
            dialogVisible:false,
           cateList:[],
           activeName:"many",
           value:[],
           manyTbale:[],
           onlyTable:[],
            CateProps:{
                 value:'cat_id',
                 label:'cat_name',
                 children:'children'
            },
            editFrom:{
               attr_name:[{required:true,message:"请输入参数名称",trigger:'blur'}]
            },
            addFrom:{
               attr_name:[{required:true,message:"请输入参数名称",trigger:'blur'}]
            }
        };
    },
    created(){
        this.getCateList()
    },
    mounted() {
        
    },
    computed:{
        titleData(){
            if(this.activeName == 'many'){
                return '动态参数'
            }
                return '静态参数' 
        },
        isBtn(){
            if(this.value.length !==3){
                return true
            }
               return false
        },
        cateId(){
            if(this.value.length == 3){
                return this.value[2]
            }
            return null
        }
    },
    methods: {
        async getCateList(){
         const {data:res} = await this.$http.get('categories');
          if(res.meta.status != 200) return this.$message.error('获取商品分类失败')
         this.cateList = res.data;
        },    
       handleChange(){
            this.getParamsData()      
        },
         addClose(){
            this.$refs.addFormRef.resetFields()
        },
      async edit(attr_id){
           const {data:res} =  await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,{params:{attr_sel:this.activeName}});
             if(res.meta.status !== 200) return this.$message.error('获取参数失败')
             this.editform = res.data;
            this.editDialogVisible = true;
        },
        editClose(){
            this.$refs.editFormRef.resetFields()
        },
        editParams(){
            this.$refs.editFormRef.validate( async v => {
                if(!v) return ;
                console.log(this.editform)
                 const {data:res} =  await this.$http.put(`categories/${this.cateId}/attributes/${this.editform.attr_id}`,{attr_sel:this.activeName,attr_name:this.editform.attr_name});
                if(res.meta.status !== 200) return this.$message.error('修改参数失败')
                this.$message.success('修改参数成功')
                this.editDialogVisible = false;
                 this.getParamsData()
            })
        },
    async  deleteAction(attr_id){
            const ress  =  await MessageBox.confirm('此操作将永久删除该参数, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).catch(err => err)
            if(ress !== 'confirm') return this.$message.info('已取消删除')
          const { data:res} = await  this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
          if(res.meta.status !== 200) return this.$message.error('删除参数失败')
           this.$message.success('删除参数成功')
            this.getParamsData()
        },
        handleClick(){
            this.getParamsData()    
        },
      async  addParams(){
            this.$refs.addFormRef.validate( async v => {
              if(!v) return
              const { data:res} = await this.$http.post(`categories/${this.cateId}/attributes`,{attr_name:this.form.attr_name,attr_sel:this.activeName})
              if(res.meta.status !== 201) return this.$message.error('添加参数失败')
              this.$message.success('添加参数成功');
              this.dialogVisible = false
              this.getParamsData()
            })
        },
        async getParamsData(){
             const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:this.activeName}})
             if(res.meta.status != 200) return this.$message.error('获取参数列表失败')
             console.log(res.data)
             res.data.forEach(v => {
                 v.attr_vals = v.attr_vals ? v.attr_vals.split(',') : [];
                 v.inputVisible = false
                 v.inputValue = ''
             }) 
             console.log(res.data)
                if(this.activeName == 'many'){
                    this.manyTbale = res.data
                }else{
                     this.onlyTable = res.data
                }
        } ,
      async  handleInputConfirm(row){
            if(row.inputValue.trim().length  === 0){
                row.inputValue = ''
                row.inputVisible = false
                return
            }
            row.attr_vals.push(row.inputValue);
            row.inputValue = ''
            row.inputVisible = false
            this.saveAttrVals(row)
        },
        showInput(row){
            row.inputVisible = true;
            this.$nextTick(_ => {
            this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        async saveAttrVals(row){
              const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{attr_name:row.attr_name,attr_sel:row.attr_sel,attr_vals:row.attr_vals.join(' ')})
              if(res.meta.status != 200) return this.$message.error('修改失败')
               this.$message.success('修改成功');
        },
        handelClose(i,row){
            row.attr_vals.splice(i,1);
            this.saveAttrVals(row)
        }
    },
};
</script>